<template>
  <div class="reg">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />

    <van-form @submit="onSubmit" ref="regForm">
      <van-field
        v-model="username"
        name="username"
        label=""
        placeholder=""
        :rules="[
          { required: true, message: '' },
          { pattern: /^1[3-9]\d{9}$/, message: '' },
        ]"
      />

      <van-field
        v-model="vercode"
        name="vercode"
        :rules="[
          { required: true, message: '' },
          { pattern: /^\d{6}/, message: '' },
        ]"
        center
        clearable
        label=""
        placeholder=""
      >
        <template #button>
          <van-button
            size="small"
            @click="sendSms"
            :loading="codeBtnLoading"
            loading-type="circular"
            native-type="button"
            :loading-text="code_text"
            :disabled="codeBtnDisabled"
            type="primary"
            >{{ code_text }}</van-button
          >
        </template>
      </van-field>

      <van-field
        v-model="password"
        type="password"
        name="password"
        label=""
        placeholder=""
        :rules="[
          { required: true, message: '' },
          { validator: (e) => /^\w{6,12}$/.test(e), message: '' },
        ]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"></van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import { reg, sendSms } from "@/api/home.js";

export default {
  data() {
    return {
      username: "",
      password: "",
      vercode: "",
      codeBtnDisabled: false,
      code_text: "",
      codeBtnLoading: false,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //
    onSubmit(values) {
      // console.log("submit", values);
      reg(values)
        .then((res) => {
          console.log(res);
          this.$router.push("/login");
        })
        .catch((err) => {});
    },
    //
    async sendSms() {
      let validate_res = await this.$refs.regForm.validate("username");
      this.codeBtnDisabled = true;
      this.code_text = "...";
      this.codeBtnLoading = true;
      sendSms({
        mobile: this.username,
        type: "register",
      })
        .then((res) => {
          console.log(res);
          if (res.code == 0) {
            //
            this.$toast(`${this.username},`);
            let count = 10;
            let timer = setInterval(() => {
              count--;
              this.code_text = `${count}s`;
              if (count == 0) {
                clearInterval(timer);
                this.codeBtnDisabled = false;
                this.code_text = "";
                this.codeBtnLoading = false;
                count = 10;
              }
            }, 1000);
          } else {
            this.$toast(res.msg || "");
            this.codeBtnDisabled = false;
            this.code_text = "";
            this.codeBtnLoading = false;
          }
        })
        .catch((err) => {
          this.$toast(err.msg || "");
          this.codeBtnDisabled = false;
          this.code_text = "";
          this.codeBtnLoading = false;
        });
    },
  },
};
</script>
<style lang="less">
.reg {
  .van-nav-bar__content {
    background-color: #07c160;
  }
}
</style>
