<template>
  <div class="edit">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <!-- {{ $store.state.userInfo }} -->
    <div class="container">
      <input id="file" @change="change" type="file" hidden accept="image/*" ref="file" />
      <!-- / -->
      <van-cell title="" center>
        <label for="file"> <van-image width="50" height="50" round fit="cover" :src="userInfo.avatar" /></label>
      </van-cell>
      <!-- <van-cell title="" is-link :value="userInfo.nickname" /> -->

      <van-cell title="" is-link @click="down" :value="userInfo.nickname" />
      <van-dialog v-model="show" title="" show-cancel-button @confirm="confirm">
        <van-cell-group> <van-field v-model="value" placeholder="" /> </van-cell-group
      ></van-dialog>
      <van-cell title="" is-link @click="setPicker" :value="userInfo.sex" />
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker show-toolbar :columns="columns" @cancel="showPicker = false" @confirm="sexConfirm" />
      </van-popup>
      <van-cell title="" is-link @click="birPicker = true" :value="userInfo.birthday" />
      <van-popup v-model="birPicker" round position="bottom">
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          title=""
          :min-date="minDate"
          :max-date="maxDate"
          @change="birchange"
          @confirm="birconfirm"
          @cancel="bircancel"
        />
      </van-popup>
      <div style="margin: 30px 50px">
        <van-button round block type="primary" size="normal" native-type="submit" @click="submit"></van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { Image as VanImage } from 'vant';
import { mapActions } from 'vuex';
import { editUserInfo } from '@/api/home';
import sexpicker from './userinfo/sexpicker';
import sexbir from './userinfo/sexbir';
import { Dialog } from 'vant';
export default {
  name: 'edit',
  components: {
    [VanImage.name]: VanImage,
    [Dialog.Component.name]: Dialog.Component,
  },
  mixins: [sexpicker, sexbir],

  data() {
    return {
      userInfo: {},
      value: '',
      show: false,
    };
  },
  methods: {
    //
    down() {
      this.show = true;
    },
    mounted() {
      console.log('mou');
    },
    // keeplive  monted 
    //
    activated() {
      console.log(2);
    },
    //
    deactivated() {
      console.log(1);
    },
    //
    confirm() {
      this.userInfo.nickname = this.value;
    },
    ...mapActions(['upload']),
    //
    async submit() {
      let { _id, nickname, avatar, file, sex, birthday } = this.userInfo;
      // console.log(file instanceof Object);
      if (file) {
        //
        let fileList = [
          {
            file,
          },
        ];
        let res = await this.upload(fileList);
        avatar = res[0];
        // console.log(avatar);
        editUserInfo({
          uid: _id,
          nickname,
          avatar,
          sex,
          birthday,
        }).then((res) => {
          console.log(res);

          if (res.code == 0) {
            this.$toast(res.msg);
            // 
            //1.
            // this.$store.dispatch("getUserInfo", localStorage.getItem("token"));
            //2
            this.$store.commit('change', {
              key: 'userInfo',
              value: {
                _id,
                nickname,
                avatar,
                sex,
                birthday,
              },
            });
            setTimeout(() => {
              this.$router.push('/user');
            }, 1000);
          }
        });
      } else {
        editUserInfo({
          uid: _id,
          nickname,
          avatar,
          sex,
          birthday,
        }).then((res) => {
          console.log(res);

          if (res.code == 0) {
            this.$toast(res.msg);
            // 
            //1.
            // this.$store.dispatch("getUserInfo", localStorage.getItem("token"));
            //2.
            this.$store.commit('change', {
              key: 'userInfo',
              value: {
                _id,
                nickname,
                avatar,
                sex,
                birthday,
              },
            });
            setTimeout(() => {
              this.$router.push('/user');
            }, 1000);
          } else {
            this.$toast('');
          }
        });
      }
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    async change(e) {
      //   console.log(e.target.files);
      //vantbase64
      let file = e.target.files[0];

      this.userInfo.file = file;
      //   console.log(this.userInfo.file);
      //html5
      let fileReader = new FileReader();
      //   readAsDataURL  Blob  File readyState DONE loadend (en-US)  result data:URL base64 
      fileReader.readAsDataURL(file);
      fileReader.onload = (e) => {
        // console.log(e.target.result);
        this.userInfo.avatar = e.target.result;
      };
    },
  },
  watch: {
    //
    // watchhandlerimmediate
    '$store.state.userInfo': {
      immediate: true,
      handler(n, o) {
        // console.log(n);
        // console.log(o);
        this.userInfo = JSON.parse(JSON.stringify(n));
      },
    },
  },
  created() {
    //
    //vuexvuexuserinfo
    //
    // this.userInfo = JSON.parse(JSON.stringify(this.$store.state.userInfo));
  },
};
</script>
<style scoped lang="less">
.edit {
  .van-nav-bar {
    background-color: #3288de;
  }
  ::v-deep .van-nav-bar .van-icon {
    color: black;
  }
}
</style>
